import React from 'react'
import './styles.css'
// import { ParallaxProvider,Parallax } from 'react-scroll-parallax';
import IMG from './img/hackathon.jpg'
import IMG2 from './img/comp.jpg'
import IMG3 from './img/internship.jpg'

const Comp = () =>  {
    return(
        <div className="comp-padding" id="comp">
            
            <div className="container comp-background">
                <div className="columns">
                    <div className="column">
                        <figure className="image">
                            <img src={IMG} alt="Placeholder"/>
                        </figure>
                    </div>
                    <div className="column">
                        
                            <div className="card-content ">
                                <p className="content is-large">
                                    Hackathons
                                </p>
                                <p className="subtitle has-text-left">
                                Elevate your coding skills with our real world scenario based hackathons. Kick start your brain to think out of the box and come up with enthralling solutions, better algorithms and models. Are you scared of participating in hackathons wondering your skills might not be good enough ? Don't hesitate now. Iknowa is keen to provide you with such mind storming hackathons for beginners and pros.<br/><br/>
                                You get an oppurtunity to compete with registrants all across the globe. The one with the most optimal and efficient code wins, and <strong>you could be the one !</strong>
                                We conduct hackathons from school/college level to national level. Winners get so many perks and rewards but we even provide all our participants with some token of appreciation since by even participating in such events, you learn a lot.<br/>
                                So amp up your brain and let the coding begin !
                                </p>
                            </div>
                        
                    </div>
                </div>
            </div>

            {/* <ParallaxProvider>
            <Parallax y={[110, -90]}> */}

            <div className="container comp-background">
                <div className="columns">
                    {/* image was here */}
                    <div className="column">
                        
                            <div className="card-content">
                                <p className="content is-large">
                                    Competitions
                                </p>
                                <p className="subtitle has-text-left">
                                The Competitions at Iknowa are an excellent choice to help you practise and gain expertise. You can watch our tutorials and gain points by solving the questions. The more you solve, the more your score increases. Every member at Iknowa can compete with each other and our Dashbord scores will show you your ranking. High scores earn badges and so any rewards like Vouchers and Goodies. Collecting Badges will elevate your profile to a ProCoder.<br/><br/>

                                Tensed about technical questions at your college placements ? Our competetions consists of the kind of questions a company would want you to be able to solve. You can solve questions from GAFA (Google, Apple Facebook, Amazon) companies and boost your abilities .
                                <br/><br/>
                                <strong>"For the things we have to learn before we can do them, we learn by doing them- Aristotle"</strong>
                                </p>
                            </div>
                        
                    </div>
                    <div className="column">
                        <figure className="image">
                            <img src={IMG2} alt="Placeholder"/>
                        </figure>
                    </div>
                </div>
            </div>

            
            {/* <Parallax y={[130, -90]}> */}
            <div className="container comp-background comp-padding-2">
                <div className="columns">
                    <div className="column">
                        <figure className="image">
                            <img src={IMG3} alt="Placeholder"/>
                        </figure>
                    </div>
                    <div className="column">
                        
                            <div className="card-content">
                                <p className="content is-large">
                                    Internship
                                </p>
                                <p className="subtitle has-text-left">
                                Do you want to gain some industry relevant knowledge and skills besides your school/university syllabus ? If your answer is Yes, then Iknowa is the right place for you.<br/><br/>
                                We provide internships for students with or without the pre-requisites. We welcome you to do internships with us and our associates. We also provide you with the best and on-demand courses from well known renowned companies, to give you best experience. Get started and flaunt these internship certificates in your resume .
                                </p>
                            </div>
                        
                    </div>
                </div>
            </div>
            {/* </Parallax>
            </Parallax>
            </ParallaxProvider> */}
        </div>
    )
}

export default Comp