import React from 'react'
import './styles.css'
import 'bulma'
import logo from "./img/logo.png"


function navclick(){
    document.getElementById("nav-menu").classList.toggle('is-active')
    document.getElementById("navbar-burger").classList.toggle('is-active')
}

// function scrollthing(e){
//     e.preventDefault()
// }

const Navbar = () =>  {
    return(
        <div>   
            <nav className="navbar is-transparent is-fixed-top content is-medium">
                <div className="navbar-brand">
                    <a className="navbar-item" href="http://localhost:3000/#">
                        <img src={logo} alt="Bulma: a modern CSS framework based on Flexbox"/>
                    </a>
                <div className="navbar-burger" id="navbar-burger" data-target="nav-menu" onClick={navclick}> 
                    {/* function calling to changing menu into cross when open in phone */}
                    <span></span>
                    <span></span>
                    <span></span>
                    </div>
                </div>
                <div id="nav-menu" className="navbar-menu">
                    <div className="navbar-start">
                        <a className="navbar-item" href="#headbar">Home</a>
                        <a className="navbar-item" href="#services" >Services</a>
                        <a className="navbar-item" href="#comp" >Competitions</a>

                            {/* <div className="navbar-item has-dropdown is-hoverable"> 
                            <a className="navbar-link" href="127.0.0.1" onClick={scrollthing}>Competitions</a>
                                <div className="navbar-dropdown is-boxed">
                                    <a className="navbar-item" href="http://localhost:3000/#" onClick={scrollthing}>Ranking Series</a>
                                    <a className="navbar-item" href="http://localhost:3000/#" onClick={scrollthing}>Internships</a>
                                    <hr className="navbar-divider"/>
                                    <a className="navbar-item" href="http://localhost:3000/#" onClick={scrollthing}>Can you solve this ?</a>
                                    <a className="navbar-item is-active" href="http://localhost:3000/#" onClick={scrollthing}>More to come ..</a>
                                </div>
                            </div> */}
                    </div>
                    <div className="navbar-end">
                        <div className="navbar-item">
                            <div className="field is-grouped">
                                <p className="control">
                                    <a className="button is-link" href="#connect">
                                    {/* <span className="icon">
                                        <i className="fas fa-download"></i>         adding any icon in the connect button
                                    </span> */} 
                                    <span>Connect with us</span>
                                    </a>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Navbar