import React from 'react'
import './styles.css'

function campusamb(e){
    e.preventDefault()
}

const Campus = () =>  {
    return(
        <div className="campus-style">
            <div className="columns">
            <div className="column is-half">
                

            </div>
            <div className="column is-half">
                <div className="tile intro-title-card">
                    <div className="card">
                        <div className="card-content">
                            <p className="content is-large">
                                Campus
                            </p>
                            <p className="subtitle has-text-left content is-medium">
                            Do you have leadership qualities? Do you like conducting events ? Would want some amazing organizer perks? Then you could be the CAMPUS AMBASSADOR for IKNOWA at your school/college. Your responsibilities would include :<br/>
                                1. Be the voice of Iknowa and represent at your school/college<br/>
                                2. Conduct seminars and sessions<br/>
                                3. Organize Hackathons and competitions in association with Iknowa at your campus<br/><br/>
                                Your perks/ rewards :<br/>
                                1. Certificate<br/>
                                2. Chance to do an internship with us<br/>
                                3. Free goodies, vouchers and memberships
                            </p>
                        </div>
                            <p className="card-footer-item">
                                <div>
                                    <a className="is-link content is-medium" href="127.0.0.1" onClick={campusamb}>Coming Soon ...</a><br/><br/>
                                </div>
                            </p>
                    </div>
                </div>
            </div>
            </div>
            <br/><br/>
        </div>
    )
}

export default Campus