import React from 'react'
import './styles.css'
import 'bulma'
import Typed from 'react-typed'
import Particles from 'react-particles-js'

const Headbar = () =>  {
    return(
        <div className="headbar" id="headbar">
            <div className="content is-large"><h3>Together we <Typed strings={["learn","code","grow","create","achieve","exceed","INNOVATE"]} typeSpeed={80} backSpeed={60} loop/></h3></div>
            <Particles
                width='100%'
                height='600px'
                params={{
                    "particles": {
                        "number": {
                            "value": 70
                        },
                        "color":{
                            "value": ["#BD10E0","#B8E986","#50E3C2","#FFD300","#E86363"]
                        },
                        "line_linked":{
                            "color": '#c8c8c8',
                            "opacity": 0.7
                        },
                        "size": {
                            "value": 3
                        }
                    },
                    "interactivity": {
                        "events": {
                            "onhover": {
                                "enable": true,
                                "mode": "repulse"
                            }
                        }
                    },

                }}
            />
        </div>
    )
}

export default Headbar